@import './variables.scss';

body {
  padding: 0;
  margin: 0;
  font-family: GothamHTF-Book;
}

.App {
  width: 100vw;
  overflow: hidden;
}

@font-face {
    font-family: GothamHTF-Bold;
    src: url("./fonts/GothamHTF-Bold.otf") format("opentype");
}

@font-face {
    font-family: GothamHTF-Book;
    src: url("./fonts/GothamHTF-Book.otf") format("opentype");
}

@font-face {
    font-family: GothamHTF-BookItalic;
    src: url("./fonts/GothamHTF-BookItalic.otf") format("opentype");
}

@font-face {
    font-family: GothamHTF-Medium;
    src: url("./fonts/GothamHTF-Medium.otf") format("opentype");
}

.font-medium {
  font-family: GothamHTF-Medium;
}

.font-bold {
  font-family: GothamHTF-Bold;
}

.font-italic {
  font-family: GothamHTF-BookItalic;
}

.font-os {
  font-family: "Open Sans";
}

.uppercase {
  text-transform: uppercase;
}

.center {
  display: flex;
  justify-content: center;
}

.vertical-center {
  display: flex;
  align-items: center;
  height: 100%;
}

.ta-center {
  text-align: center;
}

.right {
  display: flex;
  justify-content: flex-end;
}

.spaced {
  white-space: pre;
}

.container-fluid {
  padding: 0 !important;
}

.row {
  margin: 0 !important;
}

.col-xs-12 {
  padding: 0 !important;
}

input[type=text], select {
  box-sizing: border-box;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  width: 60%;
  padding: 12px 0px;
  display: inline-block;
  border: 0;
  border-bottom: 1px solid $footerGray;
  background-color: transparent;
  color: $footerGray;
  font-family: GothamHTF-Book;
}

input:focus, textarea:focus {
  outline: none;
}

input[type=submit] {
  box-sizing: border-box;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  width: 40%;
  background-color: transparent;
  color: white;
  padding: 12px 10%;
  margin: 8px 0;
  border: 2px solid $footerGray;
  cursor: pointer;
  margin-left: 5px;
  font-family: GothamHTF-Bold;
  text-transform: uppercase;
}

#header {
  width: 100vw;
  height: 125px;
  border-bottom: 1px solid $grayD;
  overflow: hidden;
  white-space: nowrap;
  .top-bar {
    height: 35px;
    background-color: black;
    color: white;
    font-size: 0.6em;
    letter-spacing: 1px;
  }
  .bottom-area {
    height: 90px;
  }
  .rings svg {
    position: absolute;
    width: 110px; 
  }
  .header-left-area > span {
    margin-left: 50px;
  }
  .right > span, .icon, .icons {
    margin-right: 25px;
  }
  .xs-menu {
    display: none;
  }
}


#prod-details {
  border: 1px solid black;
  margin-left: 3.5vw;
  width: 93vw;


}

#features {
  border: 1px solid black;
    margin-left: 3.5vw;
  width: 93vw;

}

#recProds {
  margin: 0 0 80px 0;
  padding: 35px 3.47vw;
  background-color: $grayF;
  // margin: 50px 0;
  font-family: GothamHTF-Medium;
  color: $grayX;
  h2 {
    padding: 10px 0 25px 0;
  }
}

#recs {
  .bg-img {
    position: relative;
    width: 22vw;
    height: 26.2vw;
    background-position: 50% 50%;
    background-size: cover;
    background-repeat: no-repeat;
    .tag-text {
      position: absolute;
      margin: 0.42vw;
      padding: 6px;
      background-color: white;
      text-transform: uppercase;
      font-size: 0.5em;
    }
  }
  .rec-caption {
      h5, h6 {
        margin: 7px 0 0 0;
        padding:0;
      }
      h5 {
        color: $grayX;
      }
      h6 {
        color: $medGray;
      }
  }

}

#footer {
  font-size: 0.7em;
  white-space: nowrap;
  .footer-top {
    margin: 0 !important;
    height: 57px;
    color: $grayC;
    background-color: $grayF;
    display: flex;
    width: 100%;
  }
  .footer-top > div {
    height: 30px;
    align-items: center;
    border-right: 1px solid $grayC;
  }
  .footer-top > div:last-child {
    border: none;
  }
  .footer-middle {
    background-color: black;
    color: $footerGray;
    min-height: 200px;
    width: 100vw;
    display: flex;
    flex-wrap: wrap;
    padding: 20px 0;
    h4 {
      text-transform: uppercase;
      font-family: GothamHTF-Bold;
      margin-top: 0;
      margin-bottom: 1em;
    }
    ul {
      list-style-type: none;
      padding-left: 0pt;
      line-height: 200%;
    }
    .sm-sections, .input-sections {
      //height: 150px;
      display: flex;
    }
    .sm-sections {
      width: 51.4vw;
      min-width: 320px;
      padding: 10px 0 10px 10px;
      .sm-section {
        width: 25%;
        //height: 100%;
      }
    }
    .input-sections {
      width: 38.5vw;
      min-width: 360px;
      padding-bottom: 8px;
      .nl-section {
        width: 53.2%;
        min-width: 260px; 
        input[type=text] {
          width: calc(53.2% - 43px);
        }
      }
      .stores-section {
        width: 46.2%;
        min-width: 150px;
      }
      .icons {
        margin-left: 0;
        margin-top: 15px;
      }
      .icons>svg:first-child {
        margin-right: 20px;
      }
    }
  }
  .footer-bottom {
    color: $medGray;
    background-color: black;
    a {
      text-decoration: underline;
      color: $medGray;
    }
    height: 60px;
    position: relative;
    width: 100vw;
  }
  .footer-bottom:before{
    position: absolute;
    width: 90vw;
    height: 100%;
    left: 5vw;
    top: 0;
    content: '';
    border-top: 1px solid $medGray;
  }

}

@media only screen and (min-width: 576px) {
  .xs-visible {
    display: none !important;
  }
}

@media only screen and (max-width: 575px) {
  .xs-newline:after {
    content: '\a';
    white-space: pre;
  }
  body {
    font-size: 0.8em;
  }
  .xs-hidden {
    display: none !important;
  }
  .bottom-area {
    height: 70px;
  }
  #header {
    height: 115px;
    .icon {
      margin-right: 5vw;
    }
    .icons {
      margin-right: 0;
    }
    .menu-bars {
      margin-left: 15px;
    }
    .xs-menu > div {
      background-color: white;
      padding: 12px;
      height: 20px;
      border-bottom: 1px solid $grayD;
      z-index: 50;
    }
    .xs-menu {
      display: block;
      height: 60px;
      width: 100px;
      position: absolute;
      margin-top: 15px;
      border: 1px solid $grayD;
      transform: scaleY(0);
      transition: transform 0.5s ease-in-out;
      transform-origin: top;
    }
    .active.xs-menu {
      transform: scaleY(1);
    }
    .menu-bars span, .menu-bars span:before, .menu-bars span:after {
      cursor: pointer;
      width: 18px;
      height: 2px;
      background-color: black;
      position: absolute;
      display: block;
      content: '';
    }
    .menu-bars span:before {
      top: -7px; 
    }
    .menu-bars span:after {
      bottom: -7px;
    }

    .menu-bars span, .menu-bars span:before, .menu-bars span:after {
      transition: all 0.5s ease-in-out;
    }
    .menu-bars.active > span {
      background-color: transparent;
    }
    .menu-bars.active span:before, .menu-bars.active span:after {
      top: 0;
    }
    .menu-bars.active span:before {
      transform: rotate(45deg);
    }
    .menu-bars.active span:after {
      transform: rotate(-45deg);
    }
  }
  #prod-details {

  }

  #recProds {
    padding: 8vw 2.4vw 3vw 2.4vw;
    margin-bottom: 16vw;
    text-align: center;
    h2 {
      padding: 0 0 15px 0;
      margin: 15px 0;
    }

  }

  #recs {
    .bg-img {
      width: 46.666667vw;
      height: 53.333333vw;
      .tag-text {
        padding: 8px;
        margin: 1.6vw;
      }
    }
    .rec-caption {
      margin-bottom: 5vw;
      text-align: left;
    }
  }

  #footer { 
    .footer-top {
      height: 60px;
    }
    .footer-top > div:nth-child(2) {
      border: none;
    }
    .footer-middle {
      display: block;
      padding: 0;
      h4 {
        margin-bottom: -8px;
      }
      .input-sections {
        display: block;
        position: relative;
        width: 100vw !important;
        .nl-section {
          padding: 55px 10vw 40px 10vw;
          font-size: 1.3em;
          width: 80% !important;
          input[type=text] {
            padding-left: 0;
            padding-right: 0;
            width: calc(70% - 40px) !important;
          }
        }
      }
    }
    .xsf-menu {
      font-size: 1.3em;
      width: 100%;
      position: relative;
      height: 200px;
      padding: 0px 10vw 10px 10vw;
      display: flex;
      flex-direction: column;
      flex-wrap: wrap;
      height: 222px;
      .xsf-section {
        min-height: 28px;
        padding-bottom: 18px;
        //border: 1px solid red;
      }
    }
    
  }

  #features {

  }



}
